/*Elementos generales*/

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Share Tech Mono', sans-serif;
    text-shadow: 0 0 8px rgb(255 0 0 / .2),
        -2px -2px 6px rgb(0 255 0 / .2),
        2px 2px 4px rgb(0 255 255 / .2);
    color: rgb(130, 187, 135);
    font-size: responsive 12px 21px;
}

body {

    padding: 0;

    overflow: hidden;
    background-image: url(../img/portadabackground.png);
    background-size: cover;
}

a:hover {
    color: white;
}

.current{
    background-color: white;
    pointer-events: none;
    cursor: not-allowed;
}

.container-cover {

    min-height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.content-cover {

    background-color: rgba(2, 5, 7, 0.8);
    width: 40vw;
    text-align: center;
    top: 10%;
    position: absolute;
    right: 0;
    margin-right: rfs(10vw);
    overflow-y: hidden;
    overflow-x: hidden;
    border: 2px solid black;
}

.nav-scroll-gen ul {
    background-color: rgba(2, 5, 7);
    list-style: none;
    background-position: right;
    display: grid;
    direction: row;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.nav-scroll-gen ul li {
    border: 1px black solid;
    padding: 0px;
}

.nav-scroll-gen ul li a {
    text-decoration: none;
    color: rgb(130, 187, 135);
    text-transform: uppercase;
    margin: 0px;
    width: 100%;
    height: 100%;
    line-height: 5vh;
    display: block;
    text-align: center;
}



.nav-scroll-gen ul li:hover,
.nav-scroll-gen ul li a:hover {
    background-color: white;
    color: black;

}



.text-cover {
    overflow-y: scroll;
    scrollbar-color: black rgb(130, 187, 135);
    overflow-x: hidden;
    height: 75vh;
    padding-left: 5%;
    padding-right: 5%;
    text-align: justify;
    line-height: 2rem;
}

.header h1 {
    text-align: center;
    font-size: 3rem;
    padding: 5% 5% 2.5% 5%;

}

.header h2 {
    text-align: center;
    font-size: 1.5rem;
    padding: 2.5% 5% 5% 5%;
}

header h1 a {
    text-decoration: none;
    color: white;
    letter-spacing: 1vw;
}

.footer-cover {
    position: fixed;
    bottom: 0px;
    right: 13%;
    color: white;
}

/*Categoria*/

.container-gen {
    padding-left: 10vw;
    padding-right: 10vw;
    height: 100vh;
    margin-top: 1vh;
    margin-bottom: 1vh;

}

.header-gen {
    padding-top: 5vh;
    padding-bottom: 2vh;
    background-color: rgba(2, 5, 7, 0.8);
    text-align: center;
}

.header-gen h1 a {
    font-size: 2rem;
    text-align: left;

}

.header-gen h2 {
    font-size: 1.5rem;

}

.category-wrapper {
    height: 80vh;
    border: 2px solid black;
    display: flex;
}



.categorias {
    text-align: left;
    width: 25vw;

}

.categorias ul{
    list-style-type: none;

}

.categorias ul li a {
    background-size: cover;
    background-position: center;
    cursor: pointer;
    outline: none;
    text-transform: uppercase;
    font-size: 3vw;
    font-weight: 700;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
color:  rgb(140, 196, 143);
    border-color:  rgb(130, 187, 135);
    align-items: center;
    justify-content: center;
    display:flex;
    flex-direction: column;
    height: 19.95vh;
    text-decoration: none;
}



.categorias ul li a:hover {
    filter: brightness(125%);
}

.mark {
    background-image: url(../img/mark.jpg);

}

.helly {
    background-image: url(../img/helly.jpg);

}

.dylan {
    background-image: url(../img/dylan.png);

}

.irving {
    background-image: url(../img/irving.jpg);

}

.text-category {
    text-align: justify;
    overflow-y: scroll;
    scrollbar-color: black rgb(130, 187, 135);
    overflow-x: hidden;
    background-color: rgba(2, 5, 7, 0.8);
    padding-left: 5%;
    padding-right: 5%;
    width: 100%;
    text-align: center;
    align-items: center;
}

.text-placeholder {
    padding-top: 40%;
}

#current {
    filter: brightness(125%);
    pointer-events: none;
    cursor: not-allowed;
}

article {
    text-align: left;
}

article h3 {
    text-align: left;
    padding-bottom: 0rem;
    padding-left: 1rem;
    text-decoration: underline;
}

article h2 {
    padding-top: 1rem;
    padding-bottom: 2rem;
    text-align: center;
}


article p {
    line-height: 2rem;
    padding: 1rem;
}

article a {
    color: inherit;
    text-decoration: none;

}


.trailer {
    margin-top: 2rem;
    margin-bottom: 2rem;
    text-align: center;
}

.detalles {
    padding: 2rem;
    line-height: 2rem;

}

figure{
    text-align: center;
}

table{
    padding: 1rem;
}

table *{
    padding-top:2rem;
}

table thead th{
    border: 1px solid;
    text-align: center;
    padding-bottom: 1.5%;
    padding-top: 1.5%;
}

table tbody * {
    border: 1px solid;
    text-align: center;
    padding-bottom: 1.5%;
    padding-top: 1.5%;
}